.left-arrow {
  position: absolute;
  margin-top: 380px;
  margin-left: 250px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 30px;
}

.right-arrow {
  position: absolute;
  margin-top: 380px;
  margin-left: 300px;

  color: #000;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 30px;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

.topnav {
